<template>
	<view>
		<card :headTitle="item.label" bodyStyle="background:#ffffff;" v-for="(item,index) in list" :key="index">
			<uniListItem :title="listItem.title" :showExtraIcon="true" v-for="(listItem,itemIndex) in item.value" :key="itemIndex" @click="navigate(listItem.path)">
				<view slot="right" class="iconfont icon-you text-light-muted"></view>
			</uniListItem>
		</card>
		
		<view class="p-3">
			<button type="default" class="bg-white">退出登录</button>
		</view>
	</view>
</template>

<script>
	import card from '@/components/common/card.vue'
	import myline from '@/components/common/myline.vue'
	import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue'
	
	export default {
		components:{
			card,
			myline,
			uniListItem
		},
		data() {
			return {
				list:[
					{
						label:'账号管理',
						value:[
							{title:'个人资料', path:''},
							{title:'收货地址', path:''}
						]
					},
					{
						label:'关于',
						value:[
							{title:'关于商城', path:'about'},
							{title:'意见反馈', path:''},
							{title:'协议规则', path:''},
							{title:'资质证件', path:''},
							{title:'用户协议', path:''},
							{title:'隐私政策', path:''}
						]
					}
				]
			}
		},
		methods: {
			navigate(path){
				if(!path) return{};
				uni.navigateTo({
					url: `/pages/${path}/${path}`
				});
			}
		}
	}
</script>

<style>
	page{
		background: #EEEEEE;
	}
</style>
